import { Component, OnInit } from '@angular/core';
import { EddEducationEmployeeComponent } from './edd-education-employee/edd-education-employee.component';
import { AddEducationEmployeeComponent } from './add-education-employee/add-education-employee.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';
import { EmpProfileserviceService } from 'src/app/core/services/emp-profileservice.service';
import FileSaver from "file-saver";
import { saveAs } from "file-saver/dist/FileSaver";

@Component({
  selector: 'app-employee-education',
  templateUrl: './employee-education.component.html',
  styleUrls: ['./employee-education.component.scss']
})
export class EmployeeEducationComponent implements OnInit {
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public educationData: any;
  public userId: any;
  public profilePic = "";
  public educationId: any;
  public course = {
    "courseName": "BE",
    "startDate": "11/08/2023",
    "endDate": "15/08/2023"
  }
  DataLink: any;

  constructor(public dialog: MatDialog, public rolesPermissionsService: RolesPermissionsService,
    private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, public educationService: EmpProfileserviceService) { }

  ngOnInit(): void {

    this.getEmployeeByEmployeeId();
    this.educationService.leadAdded$.subscribe(() => {
      this.getEducationDetails();
    });
    this.getEducationDetails();
  }
  getEducationDetails() {
    this.educationService.getAllEducationDetails({}, this.currentEmployee.id).then((res) => {

      this.educationData = res.data;
      console.log("data length",this.educationData.length)

    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  getEmployeeByEmployeeId() {
    this.rolesPermissionsService.getEmployeeById({}, this.currentEmployee.id).then((res) => {


      this.userId = res.data.parentId
    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  openADDEducation(userId) {
    const dialogRef: MatDialogRef<AddEducationEmployeeComponent> = this.dialog.open(AddEducationEmployeeComponent, {
      width: '700px',
      height: 'auto',
      data: { userId: userId },
      panelClass: 'custom-dialog-educationAdd-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
  openEditEducation(educationId, userId) {
    const dialogRef: MatDialogRef<EddEducationEmployeeComponent> = this.dialog.open(EddEducationEmployeeComponent, {
      width: '700px',
      height: 'auto',
      data: { educationId: educationId, userId: userId },
      panelClass: 'custom-dialog-educationUpdate-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
  getEducationId(educationId) {
    this.educationId = educationId

  }
  deletEductionById() {
    this.educationService.deleteEducation({}, this.educationId).then(res => {
      this.toastService.openSnackBar(" Education Deleted Successfully")
      this.getEducationDetails();

    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  GotoImageOrPDF(imagelink){
    this.DataLink=imagelink
    const pdfName = "invoice.pdf";
    FileSaver.saveAs(this.DataLink, pdfName);


  }


}
