import { TitleCasePipe } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { EmpProfileserviceService } from 'src/app/core/services/emp-profileservice.service';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-add-education-employee',
  templateUrl: './add-education-employee.component.html',
  styleUrls: ['./add-education-employee.component.scss']
})
export class AddEducationEmployeeComponent implements OnInit {
  public educationForm: any;
  public userId: any;
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  productPicUrl: any;
  public submitted: boolean = false;

  constructor(public dialogRef: MatDialogRef<AddEducationEmployeeComponent>, private cdr: ChangeDetectorRef, private formBuilder: FormBuilder, public router: Router, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private educationService: EmpProfileserviceService, @Inject(MAT_DIALOG_DATA) public UserData: any, private productService: ProductService,) { }

  ngOnInit(): void {
    this.userId = this.UserData.userId

    this.createEductionForm();

  }
  createEductionForm(): void {
    this.educationForm = this.formBuilder.group({
      university : ['',[Validators.required]],
      qualificationType: ['', [Validators.required]],
      courseName: ['', [Validators.required]],
      courseType: ['', [Validators.required]],
      courseStartDate: ['', [Validators.required]],
      courseEndDate: ['', [Validators.required]],
      //uploadDocument: [""],


    });
  }
  get f() {
    return this.educationForm.controls;
  }

  postEducation() {
    this.submitted = true;
    if (this.educationForm.invalid) {
      this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {
      let Body =
      {
        "addEducation": [
          {
            "university": this.educationForm.controls.university.value,
            "qualificationType": this.educationForm.controls.qualificationType.value,
            "courseName": this.educationForm.controls.courseName.value,
            "courseType": this.educationForm.controls.courseType.value,
            "startDate": this.educationForm.controls.courseStartDate.value,
            "endDate": this.educationForm.controls.courseEndDate.value,
            "uploadDocument": this.productPicUrl,
            "userId": this.userId
          }
        ]
      }
      this.educationService.addEducationDetails(Body, this.currentEmployee.id).then(res => {
        this.educationService.notifyprofileAdded();
        this.toastService.openSnackBar(" Education Added Successfully")
        this.dialogRef.close();

      }, (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      })

    }
  }
  openDatepickerstart() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.educationForm.get('courseStartDate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.educationForm.get('courseStartDate').setValue(new Date());
    }
  }

  openDatepickerend() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.educationForm.get('courseEndDate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.educationForm.get('courseEndDate').setValue(new Date());
    }
  }
  cancel() {
    this.dialogRef.close();
  }
  documentChange(e) {
    let formdata = new FormData();
    let reader = new FileReader();
    let file = e.target.files[0];

    if (e.target.files && e.target.files[0]) {
      reader.readAsDataURL(file);
      if (file == null) {
        this.toastService.openErrorSnackBar(
          "Please Select Image To Upload.",
        );
      } else {
        formdata.set("upload", file)
        this.productService.PostImagePdf(formdata).then((res) => {
          this.productPicUrl = res.data.location;
          console.log("this.productPicUrl", this.productPicUrl);

        });
      }
    }
  }
}


