import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, ValidatorFn, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { EmpProfileserviceService } from 'src/app/core/services/emp-profileservice.service';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-edit-idform',
  templateUrl: './edit-idform.component.html',
  styleUrls: ['./edit-idform.component.scss']
})
export class EditIDFormComponent implements OnInit {
  public idcForm: any;
  productPicUrl: any;
  userId: any;
  public IdsId: any
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public submitted: boolean = false;
  selectType: string;

  constructor(public dialog: MatDialog, public dialogRef: MatDialogRef<EditIDFormComponent>, private formBuilder: FormBuilder,
    private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private educationService: EmpProfileserviceService, @Inject(MAT_DIALOG_DATA) public UserData: any, private productService: ProductService,) { }

  ngOnInit(): void {
    this.userId = this.UserData.userId;
    this.IdsId = this.UserData.IDsId;
    this.createIdcForm()
    this.getSingleIdDataById();
  }
  getSingleIdDataById() {
    this.educationService.GetIdDataBYId({}, this.IdsId).then((res: any) => {
      this.selectType = res.data.idType,
      console.log("this.selectType",this.selectType)
      this.idcForm.patchValue({
        selectIdtype: res.data.idType,
        enterId: res.data.enterId,
        uploadDocument: res.data.uploadDocument,
      })
    })

  }
  createIdcForm() {
    this.idcForm = this.formBuilder.group({
      selectIdtype: ['', [Validators.required]],
      enterId: ['', [Validators.required,this.alphabeticOnlyValidator()]],
      //uploadDocument: [""],

    });

  }
  get f() {
    return this.idcForm.controls;
  }
  alphabeticOnlyValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
      const value = control.value;
     
      if (this.selectType === 'Aadhaar' && !/^[0-9]{12}$/.test(value)) {
        return { invalidAadhar: true };
      }
      else if (this.selectType === 'Passport' && !/^[a-zA-Z0-9]{8}$/.test(value)) {
        return { invalidPassport: true };
      }
      else if (this.selectType === 'Driving License' && !/^[A-Z0-9]{15}$/.test(value)) {
        return { invalidDrivingLicense: true };
      }
      else if (this.selectType === 'Election Commission ID Card' && !/^[A-Z0-9]{10}$/.test(value)) {
        return { invalidElection: true };
      }
      else if (this.selectType === 'Income Tax PAN Card' && !/^[A-Z0-9]{10}$/.test(value)) {
        return { invalidPAN: true };
      }
      else if (this.selectType === 'Ration Card with Photo, for the person whose photo is affixed' && !/^[0-9]{6}$/.test(value)) {
        return { invalidRation: true };
      }
      
      return null;
    };
  }

  getoptionType(event) {
    this.selectType = event.source.value;
  }
  postIdc() {
    this.submitted = true;
    if (this.idcForm.invalid) {
      this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {
      let Body =
      {

        
          "type": "ID",
          "idType": this.idcForm.controls.selectIdtype.value,
          "enterId": this.idcForm.controls.enterId.value,
          "uploadDocument": this.productPicUrl,
          //"useItAs": "",
          "employeeId": this.currentEmployee.id
        

      }
      this.educationService.UpdateIDCORCert(Body, this.IdsId).then((res: any) => {
        this.educationService.notifyprofileAdded();
        this.toastService.openSnackBar("Document Id Updated Successfully")

        this.dialogRef.close();
      }, (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      }
      )

    }
  }
  cancel() {
    this.dialogRef.close();
  }
  documentChange(e) {
    let formdata = new FormData();
    let reader = new FileReader();
    let file = e.target.files[0];

    if (e.target.files && e.target.files[0]) {
      reader.readAsDataURL(file);
      if (file == null) {
        this.toastService.openErrorSnackBar(
          "Please Select Image To Upload.",
        );
      } else {
        formdata.set("upload", file)
        this.productService.PostImagePdf(formdata).then((res) => {
          this.productPicUrl = res.data.location;
          console.log("this.productPicUrl", this.productPicUrl);

        });
      }
    }

  }

}
