import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-employee-work',
  templateUrl: './employee-work.component.html',
  styleUrls: ['./employee-work.component.scss']
})
export class EmployeeWorkComponent implements OnInit {
  public ProfileData:any
  public employeeId:any;
  public DateOfJoining:any
  public assets:any;
  public designation:any;
  public jobrole:any;
  public department:any
  public profileImagepath:any;
  public status:any;
  
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));

  public profilePic="";
  constructor(private roleService:RolesPermissionsService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,) { }

  ngOnInit(): void {
    this.getProfileData();
  }
  getProfileData(){
    this.roleService.getEmployeeById({}, this.currentEmployee.id).then((res:any)=>{
      this.ProfileData=res.data;
      this.employeeId=this.ProfileData.employee.id
      this.DateOfJoining=this.ProfileData.employee.joiningDate
      this.assets=this.ProfileData.employee.assets
      this.designation=this.ProfileData.employee.designation
      this.jobrole=this.ProfileData.employee.roleAssign
      this.department=this.ProfileData.employee.department
      this.profileImagepath=this.ProfileData.profileImage;
      let empstatus=this.ProfileData.isActive
      if(empstatus == true){
        this.status="Active"
      }else{
        this.status="Inactive"
      }
   

    },(err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }
    
    )

  }

}
