import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { DocumentIdcAddFormComponent } from './document-idc-add-form/document-idc-add-form.component';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { EmpProfileserviceService } from 'src/app/core/services/emp-profileservice.service';
import { EditIDFormComponent } from './document-idc-add-form/edit-idform/edit-idform.component';

@Component({
  selector: 'app-employee-document',
  templateUrl: './employee-document.component.html',
  styleUrls: ['./employee-document.component.scss']
})
export class EmployeeDocumentComponent implements OnInit {
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public idsData: any;
  public IdsId: any
  public searchKey: any;

  constructor(public dialog: MatDialog, public rolesPermissionsService: RolesPermissionsService, private toastService: ToastNotificationService, private educationService: EmpProfileserviceService,
    private titleCasePipe: TitleCasePipe,) { }

  ngOnInit(): void {
    this.getEmployeeByEmployeeId();
    this.educationService.leadAdded$.subscribe(() => {
      this.getIDCData();
    });
    this.getIDCData();
  }
  getIDCData() {
    this.educationService.getIDOrCERT({}, this.currentEmployee.id).then((res: any) => {
      this.idsData = res.data


    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }


  getEmployeeByEmployeeId() {
    this.rolesPermissionsService.getEmployeeById({}, this.currentEmployee.id).then((res) => {
      this.userId = res.data.parentId
    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  userId(arg0: string, userId: any) {
    throw new Error('Method not implemented.');
  }


  openIdcForm(userId) {
    const dialogRef: MatDialogRef<DocumentIdcAddFormComponent> = this.dialog.open(DocumentIdcAddFormComponent, {
      width: '700px',
      height: 'auto',
      data: { userId: userId },
      panelClass: 'custom-dialog-IDC-FORM-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });


  }
  openEditEducation(IDsId, userId) {
    const dialogRef: MatDialogRef<EditIDFormComponent> = this.dialog.open(EditIDFormComponent, {
      width: '700px',
      height: 'auto',
      data: { IDsId: IDsId, userId: userId },
      panelClass: 'custom-dialog-IDC-FORM-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

  getIDId(IdsId: any) {
    this.IdsId = IdsId
  }
  deletDocumentById() {
    this.educationService.DeleteIdDocuments({}, this.IdsId).then((res: any) => {
      this.toastService.openSnackBar("Document Id Deleted Successfully")
      this.getIDCData();
    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

}
