import { TitleCasePipe } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { EmpProfileserviceService } from 'src/app/core/services/emp-profileservice.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import * as moment from 'moment';
import { ProductService } from 'src/app/core/services/product.service';

@Component({
  selector: 'app-edd-education-employee',
  templateUrl: './edd-education-employee.component.html',
  styleUrls: ['./edd-education-employee.component.scss']
})
export class EddEducationEmployeeComponent implements OnInit {
  public educationForm: any;
  public  userId: any;
  public educationId:any;
  productPicUrl: any;
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public submitted: boolean = false;

  constructor(public dialogRef: MatDialogRef<EddEducationEmployeeComponent>,private cdr: ChangeDetectorRef,private formBuilder: FormBuilder, public router: Router,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, @Inject(MAT_DIALOG_DATA) public UserData: any,private educationService:EmpProfileserviceService,private productService: ProductService,) { }

  ngOnInit(): void {
    this.userId=this.UserData.userId
    this.educationId=this.UserData.educationId
    this.createEductionForm();
    this.getEducationById();

    this.educationForm.get('qualificationType').valueChanges.subscribe((newValue) => {
      // Check if the qualification type has changed to something other than 'Graduation'
      if (newValue !== 'Graduation') {
        // Reset the courseName control to an empty value
        this.educationForm.get('courseName').setValue('');
      }

      if (newValue !== 'Post Graduation') {
        // Reset the courseName control to an empty value
        this.educationForm.get('courseName').setValue('');
      }
    });
  }
  get f() {
    return this.educationForm.controls;
  }
  UpdateEducation(){
    this.submitted = true;
    if (this.educationForm.invalid) {
      this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else{
      let Body={
        "updateEducations": [
         {
           "id":this.educationId,
           "university": this.educationForm.controls.university.value,
           "qualificationType": this.educationForm.controls.qualificationType.value,
           "courseName": this.educationForm.controls.courseName.value,
           "courseType": this.educationForm.controls.courseType.value,
           "startDate": moment(this.educationForm.controls.courseStartDate.value, 'DD-MMM-YYYY').format('YYYY-MM-DD'),
           "endDate":moment(this.educationForm.controls.courseEndDate.value, 'DD-MMM-YYYY').format('YYYY-MM-DD'),
           "uploadDocument":this.productPicUrl ,
           "userId": this.userId,
         },
       ]
     }
     this.educationService.EducationUpdateByEmployeeId(Body,this.currentEmployee.id).then((res:any)=>{
       this.educationService.notifyprofileAdded();
       this.toastService.openSnackBar(" Education Updated Successfully")
       this.dialogRef.close();
     },(err) => {
       if (err.error.expose) {
         this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
       }
       else {
         this.toastService.openErrorSnackBar("Something Went Wrong.");      
       } 
      })

    }
   
  }
  openDatepickerstart() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.educationForm.get('courseStartDate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.educationForm.get('courseStartDate').setValue(new Date());
    }
  }

  openDatepickerend() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.educationForm.get('courseEndDate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.educationForm.get('courseEndDate').setValue(new Date());
    }
  }
  cancel(){
    this.dialogRef.close();
  }
  createEductionForm(){
    this.educationForm = this.formBuilder.group({
      university :  ['',[Validators.required]],
      qualificationType: ['',[Validators.required]],
      courseName: ['',[Validators.required]],
      courseType: ['',[Validators.required]],
      courseStartDate: ['',[Validators.required]],
      courseEndDate: ['',[Validators.required]],
   //uploadDocument: [""],
    });
  }
  getEducationById(){
    this.educationService.GetEducationByEducationId({},this.educationId).then((res:any)=>{

      this.educationForm.patchValue({
        university : res.data.university,
        qualificationType:res.data.qualificationType,
        courseName: res.data.courseName,
        courseType:res.data.courseType,
        courseStartDate:res.data.startDate ? (moment(res.data.startDate).format("DD-MMM-YYYY")) : (moment(res.data.startDate).format("DD-MMM-YYYY")),
        courseEndDate: res.data.endDate? (moment(res.data.endDate).format("DD-MMM-YYYY")) : (moment(res.data.endDate).format("DD-MMM-YYYY")),
        uploadDocument:res.data.uploadDocument ,

      })
      
    })

  }
  documentChange(e){
    let formdata = new FormData();
    let reader = new FileReader();
    let file = e.target.files[0];

    if (e.target.files && e.target.files[0]) {
      reader.readAsDataURL(file);
        if (file == null) {
          this.toastService.openErrorSnackBar(
            "Please Select Image To Upload.",
          );
        } else {
          formdata.set("upload", file)
          this.productService.PostImagePdf(formdata).then((res) => {
            this.productPicUrl = res.data.location;
            console.log("this.productPicUrl", this.productPicUrl);
  
          });
        }
      }

  }
}
