import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EmployeePersonalDetailsComponent } from './employee-personal-details/employee-personal-details.component';
import { EmployeeWorkComponent } from './employee-work/employee-work.component';
import { EmployeeEducationComponent } from './employee-education/employee-education.component';
import { EmployeeDocumentComponent } from './employee-document/employee-document.component';
import { MyProfileEmployeeComponent } from './my-profile-employee/my-profile-employee.component';

const routes: Routes = [
  {
    path:'',
    component:MyProfileEmployeeComponent,
    children: [
      {
        path:'',
        redirectTo: 'emp-profile',
        pathMatch: 'full'
      },
      {
        path:'emp-profile',
        component:EmployeePersonalDetailsComponent,
      },
      {
        path:'emp-work',
        component:EmployeeWorkComponent,
      },
      {
        path:'emp-education',
        component:EmployeeEducationComponent,
      },
      {
        path:'emp-document',
        component:EmployeeDocumentComponent,
    
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeprofileRoutingModule { }
