import { Injectable } from '@angular/core';
import { UrlConstants } from 'src/app/_helpers/url-constants';
import { HttpService } from './http.service';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EmpProfileserviceService {
  
  private MyProfileSource = new Subject<void>();
  leadAdded$ = this.MyProfileSource.asObservable();

  notifyprofileAdded() {
    this.MyProfileSource.next();
  }


  constructor( public httpClient:HttpService ){ }
  
  getAllEducationDetails(data:{},empId):any{
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empeductionlist + empId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  addEducationDetails(data:{},empId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empaddeduction+ empId ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  deleteEducation(data:{},educationId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empdeleteduction+ educationId ,  'DELETE').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  GetEducationByEducationId(data:{},educationId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgeteduction + educationId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
   
  }
  EducationUpdateByEmployeeId(data:{},empId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empupdateeduction+ empId ,'PUT').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  PostIDCORCert(data:{},userId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empaddidcertificate+ userId ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  UpdateIDCORCert(data:{},docId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empupdatedocument+ docId ,  'PUT').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  getIDOrCERT(data:{},empId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgetidcertificate + empId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  GetIdDataBYId(data:{},IdsId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgetdocument + IdsId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  DeleteIdDocuments(data:{},IdsId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empdeletedocument+ IdsId ,  'DELETE').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });

  }


}
