import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { PartyService } from 'src/app/core/services/party.service';
import { ProfileService } from 'src/app/core/services/profile.service';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-employee-personal-details',
  templateUrl: './employee-personal-details.component.html',
  styleUrls: ['./employee-personal-details.component.scss']
})
export class EmployeePersonalDetailsComponent implements OnInit {

  public ProfileData:any
  public firstName:any;
  public email:any;
  public mobileNumber:any;
  public address:any;
  public country:any
  public state:any
  public city:any;
  public pincode:any
  public profileImagepath:any;
  public gender:any;
  
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  employeeId: any;



  constructor(private roleService:RolesPermissionsService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, public profileService: ProfileService, public partyService : PartyService,) { }

  ngOnInit(): void {
    this.getProfileData();
  }
  getProfileData(){
    this.roleService.getEmployeeById({}, this.currentEmployee.id).then((res:any)=>{
      this.ProfileData=res.data;

      this.employeeId=this.ProfileData.employee.id
      this.firstName=this.ProfileData.firstName;
      this.email=this.ProfileData.email;
      this.mobileNumber=this.ProfileData.mobile;
      this.profileImagepath=this.ProfileData.profileImage;
      this.address=this.ProfileData.useraddresses[0].address
      this.country=this.ProfileData.useraddresses[0].country
      this.state=this.ProfileData.useraddresses[0].state
      this.city=this.ProfileData.useraddresses[0].city
      this.pincode=this.ProfileData.useraddresses[0].pincode
      this.gender=this.ProfileData.employee.gender ? this.ProfileData.employee.gender :"-"
    },(err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }
    
    )

  }
  documentChange(e){
  
      let formdata = new FormData();
      let reader = new FileReader();
      let file = e.target.files[0];
 
     

      if (e.target.files && e.target.files[0]) {
        reader.readAsDataURL(file);
        reader.onload = () => {
          this.profileImagepath = reader.result;
        };
        if (file == null) {
          this.toastService.openErrorSnackBar("Please Select Image To Upload.");
        } else {
          formdata.set("upload", file);
          this.profileService
            .postProfileImage(formdata, this.currentEmployee.id)
            .then((res) => {
              this.toastService.openSnackBar("Image Uploaded Successfully");
              this.getProfileData();
              this.partyService.notifyPartyAdded();
            });
        }
      }

  }

}
